#offline-notice {
	display: none;
	font-size: 16px;
	opacity: 0;
	position: absolute;
	text-align: center;
	transition: opacity 0.25s ease;
	top: 10px;
	width: 100%;

	&.show {
		display: block;
		opacity: 1;
	}

	svg {
		height: 20px;
		margin: 0 15px;
		position: relative;
		top: 5px;
	}
}